.container {
    background-color: rgb(238, 236, 236);
    display: flex;
    flex-direction: column;
    color: rgb(12,60,108);
    justify-content: center;
    align-items: center;
}

.download {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0 1rem;
}

.download h3 {
    margin: 1rem 0;
}

.download p {
    border-bottom: 0.1rem solid rgba(0, 0, 0, 0.377);
    padding-bottom: 1rem;
    margin-bottom: 2rem;
    width: 100%;
}

